import React, { useEffect, useState } from "react";
import { publicFetch } from "../util/fetch";
import { Link } from "react-router-dom";
import GradientBar from "./../components/common/GradientBar";
import ProductCard from "../components/products/productCardClient";

export default function Catalogue() {
  const [products, setProducts] = useState([]);

  useEffect(() => {
    const listUsers = async () => {
      const { data } = await publicFetch.get(`products`);

      setProducts(data.products);
    };

    listUsers();
  }, []);

  return (
    <>
      <div className="w-full top-0 bg-white px-5 p-3 lg:fixed z-20">
        <div className="flex justify-left">
          <div className="mt-2 mb-3">
            <h1 className="text-xl tracking-tight font-extrabold text-gray-900 sm:text-2xl md:text-2xl mr-6">
              <span className="block text-indigo-600 xl:inline uppercase">
                Sant√©x
              </span>
            </h1>
          </div>
          <div className="flex items-center ml-auto">
            <Link to="/" className="text-gradient mr-6">
              Home
            </Link>
          </div>
        </div>
        <GradientBar />
      </div>
      <div className="w-full">
        <div className="mt-20">
          <div className="flex items-center mt-5 sm:mt-10">
            <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5">
              {products.map((pd) => (
                <ProductCard key={pd._id} product={pd} />
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
